* {
  margin: 0;
  padding: 0;

  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}

body {
  font-family: "Poppins", sans-serif;
}

.wrapper {
  margin: auto;
  width: 91%;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(18em, 1fr));

  grid-gap: 2em;
}
.card {
  text-align: center;
  padding: 1em;
  overflow: hidden;
  box-shadow: 0 1px 10px gray;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  transition: transform 200ms ease-in;
}
.card-title {
  text-align: center;
  padding: 1rem;
  font-weight: bold;
}

.card-btn {
  padding: 1rem;
  cursor: pointer;
  font-family: inherit;
  font-weight: bold;
  font-size: 1rem;
  margin: 1rem;
  border: none;
  background: transparent;
  border-radius: 10px;
  color: white;
  background-color: black;
  transition: background 200ms ease-in, color 200ms ease-in;
}
.card-btn > a {
  text-decoration: none;
  color: white;
}
.card:hover {
  transform: scale(1.02);
}
.card-btn:hover {
  background-color: red;
  color: white;
}
.section-title {
  font-weight: bold;
  font-size: 50px;
  display: flex;
  justify-content: center;
  padding: 20px;
  margin: 20px;
}

/* For very low screen devices  */

@media screen and (max-width: 320px) and (min-width: 0px) {
  .section-title {
    font-size: 25px;

    padding: 8px;
    margin: 8px;
  }
  .card-title {
    padding: 0.5rem;
    font-size: 20px;
  }
  .card-btn {
    margin: 0.8rem;
    padding: 0.8rem;
  }
  .wrapper {
    width: 100%;
    margin: auto;
  }
}

/* For Most of the Smartphones */

@media screen and (max-width: 480px) and (min-width: 320px) {
  .section-title {
    font-size: 35px;

    padding: 12px;
    margin: 12px;
  }
  .wrapper {
    width: 90%;
    margin: auto;
  }
}

/* For low resolution tablets  */

@media screen and (max-width: 768px) and (min-width: 480px) {
  .section-title {
    font-size: 35px;
    padding: 15px;
    margin: 15px;
  }
}

/* For Tablets/Ipad  */

@media screen and (max-width: 1023px) and (min-width: 768px) {
  .section-title {
    font-size: 40px;

    padding: 15px;
    margin: 15px;
  }
}
